import moment from 'moment';

// 
// moment.locale(['zh-cn'])

/* 2020/04/26 */
const filter = function(value) {
  return moment(value).format('YYYY/MM/DD')
}

/* 2020426 16:00 */
const filter1 = function(value) {
  if (value > 100000) {
    return moment(value).format('lll')
  } else {
    return value
  }
}

/*  fromNow */
const fromNow = function(value) {
  return moment(value).fromNow()
}

// markdown
const filterMarkdownCode = function(value) {
  return value.replace(/`|#|@/gi, "").replace(/\s+/g, " ")
}

// 
const filterUser = function(value) {
  switch (value) {
    case 'avatar':
      return '';
    case '_id':
      return 'id';
    case 'email':
      return '';
    case 'date':
      return '';
    case 'name':
      return '';
    case 'identity':
      return '';
    case 'blog_count':
      return '';
    default:
      return value
  }
}

class DatePlugin {
  constructor() {

  }

  install(Vue, options) {
    Vue.filter('date', filter);
    Vue.filter('date1', filter1);
    Vue.filter('fromNow', fromNow);
    Vue.filter('user', filterUser);
    Vue.filter('MarkdownCode', filterMarkdownCode);
  }
}


export default new DatePlugin()